


















































import Vue from 'vue'
import { mapState } from 'vuex'
import { FETCH_EVENTS } from './store'

type Computed = {
  retention: number;
  outputs: string[];
}

export default Vue.extend<{}, {}, Computed, {}>({
  name: 'App',
  computed: mapState(['retention', 'outputs']),
  created () {
    this.$store.dispatch(FETCH_EVENTS)
  }
})
